<template>
	<div>
		<v-container class="fill-height" fluid>
			<v-row align="center" justify="center">
				<v-col cols="12" sm="8" md="4">
					<v-card class="elevation-12">
						<v-toolbar color="primary" dark flat>
							<v-toolbar-title>Signup form</v-toolbar-title>
							<v-spacer></v-spacer>
							<v-tooltip bottom>
								<template v-slot:activator="{ on }">
									<v-btn :href="source" icon large target="_blank" v-on="on">
										<v-icon>mdi-code-tags</v-icon>
									</v-btn>
								</template>
								<span>Source</span>
							</v-tooltip>
						</v-toolbar>
						<v-card-text>
							<v-form>
								<v-text-field
									v-model="id"
									:error-messages="idErrors"
									:counter="10"
									label="id"
									required
									@input="$v.id.$touch()"
									@blur="$v.id.$touch()"
								></v-text-field>
								<v-text-field
									v-model="password"
									:error-messages="passwordErrors"
									label="password"
									required
									@input="$v.password.$touch()"
									@blur="$v.password.$touch()"
								></v-text-field>
								<v-checkbox
									v-model="checkbox"
									:error-messages="checkboxErrors"
									label="Do you agree?"
									required
									@change="$v.checkbox.$touch()"
									@blur="$v.checkbox.$touch()"
								></v-checkbox>

								<v-btn class="mr-4" @click="submit">submit</v-btn>
								<v-btn @click="clear">clear</v-btn>
							</v-form>
						</v-card-text>
					</v-card>
				</v-col>
			</v-row>
		</v-container>
	</div>
</template>

<script>
import { validationMixin } from 'vuelidate'
import { required, maxLength } from 'vuelidate/lib/validators'

export default {

	props: {
		source: String,
	},
	mixins: [validationMixin],

	validations: {
		id: { required, maxLength: maxLength(10) },
		password: { required },
		checkbox: {
			checked(val) {
				return val
			},
		},
	},

	data: () => ({
		id: '',
		password: '',
		checkbox: false,
	}),

	computed: {
		idErrors() {
			const errors = []
			if (!this.$v.id.$dirty) return errors
			!this.$v.id.maxLength && errors.push('id must be at most 10 characters long')
			!this.$v.id.required && errors.push('id is required.')
			return errors
		},
		passwordErrors() {
			const errors = []
			if (!this.$v.password.$dirty) return errors
			!this.$v.password.required && errors.push('password is required')
			return errors
		},
		checkboxErrors() {
			const errors = []
			if (!this.$v.checkbox.$dirty) return errors
			!this.$v.checkbox.checked && errors.push('You must agree to continue!')
			return errors
		},
	},

	methods: {
		submit() {
			this.$store.dispatch('user/signup', { id: this.id, password: this.password })
			this.$v.$touch()
		},
		clear() {
			this.$v.$reset()
			this.id = ''
			this.password = ''
			this.checkbox = false
		},
	},
}
</script>
<style>
</style>