import vue from "../main";

export default {
  success(response, msg, block) {
    if (response.status === 200) {
      vue.$notify({
        group: "notifyApp",
        type: "success",
        duration: 1000,
        text: msg,
      });
      block();
    }
  },

  error(error, msg) {
    vue.$notify({
      group: "notifyApp",
      type: "error",
      duration: 1000,
      text: msg,
    });
  },
};
